<template>
  <div id="jq">
    <Row>
      <Col span={20}>
        <h3>
          Jquery内容
          <a target="_blank" href="https://developer.mozilla.org/zh-CN/docs/Glossary/jQuery">jquery文档</a>
        </h3>
        <div class="bx mt">
          <pre>
            <span class="color_red">失去焦点</span>
            $("input").<span class="color_red">blur</span>(function(){
              $("p").hide();
            });
            <span class="color_red">获得焦点</span>
            $("input").<span class="color_red">focus</span>(function(){
              $("p").hide();
            });
          </pre>
        </div>
      </Col>
    </Row>
  </div>
</template>
<script>
import { Row, Col } from 'iview';
export default {
  components:{
    Row, Col
  },
  data: () => ({

  })
}
</script>
<style scoped>
</style>



