import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'

import InstallVue from '@/components/vue/InstallVue'
import GrammarVue from '@/components/vue/GrammarVue'
import VueRouter from '@/components/vue/VueRouter'
import Scss from '@/components/vue/Scss'



import ES6 from '@/components/ES6/ES6' //es6 首页
import ES6List from '@/components/ES6/ES6List'  //es6 模版字符串

import React from '@/components/react/React' //react 首页
import InstallReact from '@/components/react/InstallReact' //安装 下载
import GrammarReact from '@/components/react/GrammarReact' //模版 生命周期
import ConfigureSacc from '@/components/react/ConfigureSacc' //scss
import ReactRouter from '@/components/react/ReactRouter' //路由
import Grammar from '@/components/react/Grammar' //路由

//重新布局
//html 内容区
import html from '@/components/html/html'  //html
import input from '@/components/html/input' //input
//css 内容区
import Css from '@/components/css/Css'  //css 样式
import cssStyle from '@/components/css/CssStyle' //css样式
//js 内容区
import JavaScript from '@/components/JavaScript/JavaScript' //js
import NativeJavascript from '@/components/JavaScript/NativeJavascript' //原生js
import JqueryJavascript from '@/components/JavaScript/JqueryJavascript' //Jquery
import RegExp from '@/components/JavaScript/RegExp'  //正则
//Git
import Git from '@/components/git/Git'   //git
//Vue
import vue from '@/components/vue/vue' //vue 首页
import configure from '@/components/vue/configure' //vue配置
import vueDevelopment from '@/components/vue/vueDevelopment' //vue语法


Vue.use(Router)

export default new Router({
  // mode:'history',
  routes: [
    // 主页面
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld
    },
    //html 内容
    {
      path: '/html',
      name: 'html',
      component:html,
      children:[
        {
          path: '/html',
          name: '/input',
          component: input
        }
      ]
    },
    //Css 样式 内容
    {
      path: '/Css',
      name: 'Css',
      component: Css,
      children:[
        {
          path: '/Css',
          name: 'cssStyle',
          component: cssStyle
        }
      ]
    },
    // js 内容
    {
      path: '/JavaScript',
      name: 'JavaScript',
      component: JavaScript,
      children:[
        {
          path: '/JavaScript',
          name: 'NativeJavascript',
          component: NativeJavascript
        },
        {
          path: '/JavaScript/JqueryJavascript',
          name: 'JqueryJavascript',
          component: JqueryJavascript
        },
        {
          path: '/JavaScript/RegExp',
          name: 'RegExp',
          component: RegExp
        }
      ]
    },
    // git页面
    {
      path: '/Git',
      name: 'Git',
      component: Git
    },
    // vue页面
    {
      path: '/vue',
      name: 'vue',
      component: vue,
      children:[
        // 安装页面
        {
          path: '/vue',
          name: 'installvue',
          component: InstallVue,
          //加载模版之前触发
          beforeEnter (to, rfom, next) {
            // 到哪里去，从哪里来，是否往下加载模板
            console.log(arguments,'beforeEnter')
            // 执行next()就往下加载模板
            next()
          }
        },
        //配置
        {
          path: '/vue/configure',
          name: 'configure',
          component: configure
        },
        // 生命周期  模版 页面
        {
          path: '/vue/GrammarVue',
          name: 'GrammarVue',
          component: GrammarVue
        },
        // 配置scss文件
        {
          path: '/vue/Scss',
          name: 'Scss',
          component: Scss
        },
        // 路由页面
        {
          path: '/vue/vueRouter',
          name: 'VueRouter',
          component: VueRouter
        },
        //语法
        {
          path: '/vue/vueDevelopment',
          name: 'vueDevelopment',
          component: vueDevelopment
        }
      ]
    },
    // react页面
    {
      path: '/React',
      name: 'React',
      component: React,
      children:[
        {
          path: '/React',
          name: 'InstallReact',
          component: InstallReact
        },
        {
          path: '/React/GrammarReact',
          name: 'GrammarReact',
          component: GrammarReact
        },
        {
          path: '/React/scss',
          name: 'ConfigureSacc',
          component: ConfigureSacc
        },
        {
          path: '/React/vueRouter',
          name: 'ReactRouter',
          component: ReactRouter
        },
        {
          path: 'Grammar',
          name: 'Grammar',
          component: Grammar
        }
      ]
    },
    // es6 页面
    {
      path: '/ES6',
      name: 'ES6',
      component: ES6,
      children:[
        // es6 模版字符串页面
          {
            path: '/ES6',
            name: 'ES6List',
            component: ES6List
          }
      ]
    }
  ]
})



