<template>
  <div id="vue">
    <a class="js" target="_blank" href="https://cn.vuejs.org/v2/guide/">
      Vue中文官网
    </a>
    <a class="js" target="_blank" href="https://router.vuejs.org/zh-cn/essentials/getting-started.html">vue-router详细介绍</a>
    <Row>
      <Col span="4" offset='2'>
        <div class="left">
          <Menu mode="vertical" :theme="theme1" active-name="1">
            <MenuItem name="1">
              <router-link class="router_link" to='/vue'>
                安装vue-li
              </router-link>
            </MenuItem>
            <MenuItem name="2">
              <router-link class="router_link" to='/vue/configure'>
                vue-cli一些配置
              </router-link>
            </MenuItem>
            <MenuItem name="3">
              <router-link class="router_link" to="/vue/GrammarVue">
                vue模版-生命周期
              </router-link>
            </MenuItem>
              <MenuItem name="4">
                <router-link class="router_link" to="/vue/vueRouter">
                  vue-router路由
                </router-link>
              </MenuItem>
              <MenuItem name="5">
                <router-link class="router_link" to="/vue/scss">
                  vue-cli配置scss
                </router-link>
              </MenuItem>
              <MenuItem name="6">
                <router-link class="router_link" to="/vue/vueDevelopment">
                  vue开发中的一些方法
                </router-link>
              </MenuItem>
             </Menu>
        </div>
      </Col>
      <Col span="16">
        <div class="right">
          <router-view/>
        </div>
      </Col>
    </Row>
  </div>
</template>
<script>
import { Row, Col, Menu, MenuItem} from 'iview';
export default {
  components:{
    Row, Col, Menu, MenuItem
  },
  data () {
    return {
      theme1: 'light'
    }
  }
}
</script>
<style scoped>
.navLeft{
  width: 100%;
  text-align: center;
  border: 1px solid #ccc;
}
.link_hover:hover{
  background: #ccc;
}
.router_link{
  width: 100%;
  height: 100%;
  display: block;
}
.js{
  position: relative;
  top: 10px;
  display: block;
  width: 100%;
  text-align: center;
}
  .left{
    width: 100%;
    height: 100%;
    margin-top: 20px;
    margin-bottom: 20px;
  }
  .left:hover{
    box-shadow: 0 0 9px #666;
  }
  .right{
    width: 100%;
    min-height: 220px;
    background: #fff;
    margin-top: 20px;
    margin-bottom: 20px;
    margin-left: 20px;
    padding: 20px;
  }
</style>



