<template>
  <div id="Nativejs">
    <h3>
      原生js方法以及事件
    </h3>
    <p class="bt">
      <span class="color_red">document.querySelector()</span> 获取元素Node
    </p>
    <p>
      <span class="color_red">document.querySelectorAll()</span> 获取元素数组
    </p>
      <p class="bt">
        <span class="color_red">addEventListener()</span> 添加事件
      </p>
      <p class="bt">
        <span class="color_red">e.currentTarget.xxx</span> 点击的当前元素
      </p>
      <p>
        <span class="color_red">e.target.value</span> 点击的当前元素
      </p>
      <p class="bt">
        <span class="color_red">setAttribute()</span> 属性已经存在，则更新该值
      </p>
      <p>
        <span class="color_red">getAttribute()</span> 属性已经存在，则删除该值
      </p>
      <p>
        <span class="color_red">removeAttribute()</span> 属性已经存在，则获取该值
      </p>
      <p class="bt">
        <span class="color_red">previousElementSibling</span> 前一个兄弟元素
      </p>
      <p>
        <span class="color_red">nextElementSibling</span> 后一个兄弟元素
      </p>
      <p class="bt">
        <span class="color_red">forEach((item,index)=>{})</span> 遍历数组
      </p>
      <p class="bt">
        <span class="color_red">getAttributeNode()</span> 返回指定元素的指定属性,返回值是  Attr  节点类型
      </p>
      <p class="bt">
        <span class="color_red">lastChild</span> 返回文档的最后一个子节点
      </p>
      <p class="bt">
        <span class="color_red">appendChild()</span> 向节点添加最后一个子节点
      </p>
      <p class="bt">
        <span class="color_red">createElement()</span> 可创建元素节点
      </p>
      <p class="bt">
        <span class="color_red">createTextNode()</span> 可创建文本节点
      </p>
      <p class="bt">
        <span class="color_red">event.preventDefault()</span> 阻止冒泡
      </p>
  </div>
</template>
<script>
export default {
  name: "",
  data: () => ({

  })
}
</script>
<style lang="scss" scoped>
.cssStyle{
  padding: 20px;
}
</style>



