<template>
<div id="GrammarVue">
  <h3>
    vue生命周期详解
    <a target="_blank" href="https://cn.vuejs.org/v2/guide/instance.html">官网生命周期介绍</a>
  </h3>
  <div class="bx mt">
    <p><a target="_blank" href="https://www.cnblogs.com/webbest/p/6722780.html">生命周期介绍</a></p>
    <div class="ft">
      <span class="color_red">注意事项：</span>
      Vue所有的生命周期钩子自动绑定在this上下文到实例中，因此你可以访问数据，对属性和方法进行运算。这意味着你不能使用箭头函数来定义一个生命周期方法。这是因为箭头函数绑定了父上下文，因此this与你期待的Vue实例不同
    </div>
    <div class="bg white">
      <p>
        beforeCreate
        <span class='color_yellow'>el 和 data 并未初始化 </span>
      </p>
      <p>
        created
        <span class='color_yellow'>el 和 data 并未初始化</span>
      </p>
      <p>
        beforeMount
        <span class="color_yellow">/完成了 el 和 data 初始化  模版数据未编译</span>
      </p>
      <p>
        mounted
        <span class="color_yellow">完成挂载编译</span>
      </p>
      <p>
        beforeUpdate
        <span class="color_yellow">组件更新之前</span>
      </p>
      <p>
        updated
        <span class="color_yellow">组件更新之后</span>
      </p>
      <p>
        activated
        <span class="color_yellow">keep-alive组件激活时调用。</span>
      </p>
      <p>
        deactivated
        <span class="color_yellow">keep-alive组件停用时调用</span>
      </p>
      <p>
        beforeDestroy
        <span class="color_yellow">实例销毁之间调用。在这一步，实例仍然完全可用。</span>
      </p>
      <p>
        destroyed
        <span class="color_yellow">Vue实例销毁后调用。调用后，Vue实例指示的所有东西都会解绑定，所有的事件监听器会被移除，所有的子实例也会被销毁。</span>
      </p>
    </div>
    <img class="img" src="@/assets/vueLift.png" alt="生命周期">
  </div>
</div>
</template>
<script>
export default {
  data: () => ({

  }),
  methods: {
    methodName() {
      console.log('222');
    }
  }
}
</script>
<style lang="scss" scoped>
.highlight {
  height: 320px;
  overflow-x: auto;
  background-color: #f8f8f8;
  border-radius: 2px;
  position: relative;
}
.code {
  width: 100%;
  border-collapse: collapse;
  padding: 0;
  margin: 0;
}
.bg{
  padding: 10px;
  font-weight: bold;
  background: black;
  font-size: 16px;
  box-shadow: 0 0 9px #666;
}
.white{
  color: white;
}
p span{
  font-size: 14px;
}
.ft{
  font-size: 16px;
}
.img{
  width: 100%;
}
</style>



