<template>
<div id="scss">
  <div class="bx">
    安装scss：
    <p class="color_red">
      <span class="color_blue">npm安装报gyp错,先全局安装gyp，在使用cnpm安装</span><br/>
      <span class="color_blue">npm install -g node-gyp</span><br/> npm install node-sass --save-dev
    </p>
    <p class="color_red">npm install sass-loader --save-dev</p>
    <p>打开webpack.base.config.js在rules里面加上</p>
    <pre class='color_red'>
      {
        test: /\.scss$/,
        loaders: ["style", "css", "sass"]
      },
      </pre>
  </div>
  <div class="bx mt">
    <p>使用scss将px转rem：<br/>
      <span class="color_red">$browser-default-font-size: 75px !default;//变量的值可以根据自己需求定义</span><br/>
      <span class="color_blue">html {font-size: $browser-default-font-size;}</span><br/>
      <span class="color_red">
        @function pxTorem($px){//$px为需要转换的字号
      &nbsp;&nbsp;@return $px / $browser-default-font-size * 1rem;}
      </span><br/>
      <span class="color_blue">html {
            font-size: $browser-default-font-size;
        }</span><br/>
      <span class="color_red">
          font-size: pxTorem(24px);</span>
    </p>
  </div>
</div>
</template>
<script>
export default {
  name: "",
  data: () => ({

  })
}
</script>
<style lang="scss" scoped>
</style>



