<template>
  <div id="git">
    <Row>
      <Col span="16" offset='4'>
        <div class="git_content">
          <p>创建文件夹：
            <span class="color_red">mkdir xxxx(文件名)</span>
          </p>
          <p>创建本地仓库：
            <span class="color_red">git init</span>
          </p>
          <p>查看仓库情况：
            <span class="color_red">git  status</span>
          </p>
          <p>把所以文件提交到本地仓库：
            <span class="color_red">git add .</span>
          </p>
          <p>把单个文件提交到本地仓库
            <span class="color_blue">git add xxx(文件名)</span>
          </p>
          <p>提交注释：
            <span class="color_red">git commit -m '注释说明'</span>
          </p>
          <p>创建dev分支：
            <span class="color_blue">git checkout -b dev</span>
          </p>
          <p>查看本地仓库的当前分支：
            <span class="color_blue">git branch</span>
          </p>
          <p>查看远程仓库的当前分支：
            <span class="color_blue">git branch -a</span>
          </p>
          <p>清除远程仓库链接：
            <span class="color_red">git remote rm origin</span>
          </p>
          <p>本地仓库关联远程仓库：
            <span class="color_red">git remote add origin xxx(git地址)</span>
          </p>
          <p>添加到远程仓库：
            <span class="color_red">git push -u origin master</span>
          </p>
          <p>从远程仓库copy：
            <span class="color_red">git clone xxx(git地址)</span>
          </p>
        </div>
      </Col>
    </Row>
  </div>
</template>
<script>
import { Row, Col } from 'iview';
export default {
  components:{
    Row, Col
  },
  data: () => ({

  })
}
</script>
<style scoped>
.git_content{
  background: white;
  width: 100%;
  margin: 10px;
  padding: 10px;
  box-shadow: 0 0 9px #666;
}
</style>



