<template>
  <div id="Css">
    <a class="Css" target="_blank" href="https://developer.mozilla.org/zh-CN/docs/Web/CSS">Css文档查询</a>
    <Row>
      <Col span="4" offset='2'>
        <div class="left">
          <Menu mode="vertical" :theme="theme1" active-name="1">
            <MenuItem name="1">
              <router-link class="router_link" to='/Css'>
                CSS样式
              </router-link>
            </MenuItem>
            <MenuItem name="2">
              <router-link class="router_link" to=''>
                啥也没有
              </router-link>
            </MenuItem>
              <MenuItem name="3">
                <router-link class="router_link" to="">
                  啥也没有
                </router-link>
              </MenuItem>
              <MenuItem name="4">
                <router-link class="router_link" to=''>
                  啥也没有
                </router-link>
              </MenuItem>
             </Menu>
        </div>
      </Col>
      <Col span="16">
        <div class="right">
          <router-view/>
        </div>
      </Col>
    </Row>
  </div>
</template>
<script>
import { Row, Col, Menu, MenuItem} from 'iview';
export default {
  components:{
    Row, Col, Menu, MenuItem
  },
  data () {
    return {
      theme1: 'light'
    }
  }
}
</script>
<style scoped>
.router_link{
  width: 100%;
  height: 100%;
  display: block;
}
.Css{
  position: relative;
  top: 10px;
  display: block;
  width: 100%;
  text-align: center;
}
  .left{
    width: 100%;
    height: 100%;
    margin-top: 20px;
    margin-bottom: 20px;
  }
  .left:hover{
    box-shadow: 0 0 9px #666;
  }
  .right{
    width: 100%;
    min-height: 220px;
    background: #fff;
    margin-top: 20px;
    margin-bottom: 20px;
    margin-left: 20px;
    padding: 20px;
  }
</style>



