<template lang="html">
  <div class="vueDevelopment">
    <h3>vue 语法</h3>
    <div class="bx mt">
      <p class="bg white mt10">
        <span class="color_yellow">@blur="testit"</span> 在标签中使用事件
      </p>
      <p class="bg white mt10">
        <span class="color_yellow">@blur="userCard(); verification()"</span> 使用多个事件
      </p>
      <p class="bg white mt10">
        <span class="color_yellow">:value="text_iphone"</span> 在标签中使用data数据
      </p>
      <p class="bg white mt10">
        <span class="color_yellow">{{text_iphone}}</span> 在html使用data数据
      </p>
      <p class="bg white mt10">
        <span class="color_yellow">:class="{ active: isActive}"</span> 通过data数据isActive 添加class="active"
      </p>
      <div class="bg white mt10">
        <p class="color_yellow">v-if="ismessage == 1"</p>
        <p class="color_yellow">v-else-if="ismessage == 2"</p>
        <p class="color_yellow">v-else</p>
        <p>通过data数据ismessage 匹配展示数据</p>
      </div>
    </div>
    <div class="bx mt">
      <div class="bg white">
        <p>通过js跳转</p>
        <p>this.$router.push({name:'identityCard'})</p>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data(){
    return{
      text_iphone: '{{text_iphone}}'
    }
  }
}
</script>

<style lang="css" scoped>
p{
  margin: 0;
  padding: 0;
}
.bg{
  padding: 10px;
  font-weight: bold;
  background: black;
  font-size: 16px;
  box-shadow: 0 0 9px #666;
}
.white{
  color: white;
}
.mt10{
  margin-top: 10px;
}
</style>



