<template>
<div id="GrammarVue">
  <div class="moble" @click='methodName'>
    <span class="color_red">路由模块：</span>
    <a href="https://router.vuejs.org/zh-cn/advanced/meta.html" target="_blank">详细介绍</a>
    <p>定义路由的时候可以配置 meta 字段：</p>
    <p>首先，我们称呼 routes 配置中的每个路由对象为 路由记录。路由记录可以是嵌套的，因此，当一个路由匹配成功后，他可能匹配多个路由记录</p>
    <p>一个路由匹配到的所有路由记录会暴露为 $route 对象（还有在导航守卫中的路由对象）的 $route.matched 数组。因此，我们需要遍历 $route.matched 来检查路由记录中的 meta 字段。</p>
    <figure class="highlight bx mt">
      <table>
        <tbody>
          <tr>
            <td class="code">
              <pre>
  {
    path: '/vue',
    name: 'vue',
    component: vue,
    children:[
      {
        path: '/vue',
        name: 'installvue',
        component: InstallVue,
        meta:{requiresAuth: true},
      },
      {
        path: '/vue/GrammarVue',
        name: 'GrammarVue',
        component: GrammarVue,
        children:[
          {
            path: '/vue/GrammarVue/child',
            name: 'child',
            component: child
          }
        ]
      },
      {
        path: '/vue/vueRouter',
        name: 'VueRouter',
        component: VueRouter
      }
    ]
  }
              </pre>
            </td>
          </tr>
        </tbody>
      </table>
    </figure>
    <div class="mt">
      <span class="color_red">路由勾子:</span>
      <p>全局的before 钩子函数  在router中调用：
        </br><span class="color_red">beforeEnter (to, rfom, next) {}</span>
      </p>
      <p>组件的router钩子函数 通过在next(vm=>{})获取数据  在组件进入时调用：
      </br><span class="color_red">beforeRouteEnter (to, rfom, next) {}</span>
      </p>
      <p>组件的router钩子函数 可以获取data数据 在当前路由改变，但是该组件被复用时调用：
      </br><span class="color_red">beforeRouteUpdate (to, rfom, next) {}</span>
      </p>
      <p>组件的router钩子函数 可以获取data数据 在组件离开时调用：
      </br><span class="color_red">beforeRouteLeave (to, rfom, next) {}</span>
      </p>
    </div>
  </div>
</div>
</template>
<script>
export default {
  data: () => ({
    msg:'ss',
    tolist:'aa'
  }),
  // 进入时调用
  beforeRouteEnter (to, from, next){
    next(vm => {
      // alert(vm.tolist);
    });
  },
  // 改变数据时调用
  beforeRouteUpdate (to, from, next) {
    // 在当前路由改变，但是该组件被复用时调用
    // 举例来说，对于一个带有动态参数的路径 /foo/:id，在 /foo/1 和 /foo/2 之间跳转的时候，
    // 由于会渲染同样的 Foo 组件，因此组件实例会被复用。而这个钩子就会在这个情况下被调用。
    // 可以访问组件实例 `this`
    // alert(this.msg)
  },
  // 离开时调用
  beforeRouteLeave (to, from, next) {
    // alert(this.msg)
    next();
  },
  methods: {
    methodName() {
    }
  }
}
</script>
<style scoped>

</style>



