<template>
  <div id="regExp">
    <h3>正则表达式
      <a target="_blank" href="https://developer.mozilla.org/zh-CN/docs/Web/JavaScript/Reference/Global_Objects/RegExp">正则表达式文档</a>
    </h3>
    <h3 class="ma"><a href="#">更多正则表达式>>></a></h3>
    <div class="bx mt">
      <p>1.文本框只能输入数字代码(小数点也不能输入)</p>
      <div class="bg white">
        <span class="color_red">注释:</span>
        <p>"\D"=匹配任意一个不是阿拉伯数字的字符, "g"=全局匹配;找到所有匹配</p>
        <p>"onkeyup"=按键抬起触发，"onafterpaste"=粘贴之后触发</p>
        &lt;input
          onkeyup="this.value=this.value.replace(/\D/g,'')" onafterpaste="this.value=this.value.replace(/\D/g,'')">
      </div>
      <div class="bx mt">
        <p>2.只能输入英文字母和数字,不能输入中文</p>
        <div class="bg white">
          &lt;input onkeyup="this.value=value.replace(/[^\w\.\/]/ig,'')">
        </div>
      </div>
      <div class="bx mt">
        <p>3.只能输入数字和英文</p>
        <div class="bg white">
          &lt;input onKeyUp="value=value.replace(/[^\d|chun]/g,'')">
        </div>
      </div>
      <div class="bx mt">
        <p>4.只能输入数字</p>
        <div class="bg white">
          &lt;input name="text" type="text" id="NewPage" onKeyUp="value=value.replace(/\D/g,'')" onafterpaste="value=value.replace(/\D/g,'')" >
        </div>
      </div>
      <div class="bx mt">
        <p>5.只能输入中文</p>
        <div class="bg white">
          &lt;input type="text" onkeyup="value=value.replace(/[^\u4E00-\u9FA5]/g,'')">
        </div>
      </div>
      <div class="bx mt">
        <p>6.只能输入英文</p>
        <div class="bg white">
          &lt;input type="text" onkeyup="value=value.replace(/[^\a-\z\A-\Z]/g,'')"> <br/>
          &lt;input type="text" onkeyup="value=value.replace(/[^a-zA-Z]/g,'')">
        </div>
      </div>
      <div class="bx mt">
        <p>7.校验11位手机号码</p>
        <div class="bg white">
          oninput="if(value.length>11) value=value.slice(0,11)"
        </div>
      </div>
      <div class="bx mt">
        <p>8.身份证校验</p>
        <div class="bg white">
          let reg = /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/; //验证身份证号码是否正确 <br/>
          if (reg.test(self.userIDCard) === false) {<br/>
            &nbsp;&nbsp;Toast("请重新审核身份证号码");<br/>
            &nbsp;&nbsp;self.userIDCard = ''; //清除错误的身份证号码<br/>
          }
        </div>
      </div>
      <div class="bx mt">
        <p>9.微信号验证  不能输入中文 / vue项目开发使用</p>
        <div class="bg white">
          self.WechatID = WechatID.replace(/[^\w\.\/]/ig,'');
        </div>
      </div>
      <div class="bx mt">
        <p>10.表情包验证  不能输入表情包 / vue项目开发使用</p>
        <div class="bg white">
          self.address = address.replace(/[^\u0020-\u007E\u00A0-\u00BE\u2E80-\uA4CF\uF900-\uFAFF\uFE30-\uFE4F\uFF00-\uFFEF\u0080-\u009F\u2000-\u201f\u2026\u2022\u20ac\r\n]/g,'');
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "",
  data: () => ({

  })
}
</script>
<style lang="scss" scoped>
.bx{
  box-shadow: 0 0 9px #666;
  width: 100%;
  padding: 10px;
}
.bg{
  padding: 10px;
  font-weight: bold;
  background: black;
  font-size: 16px;
  box-shadow: 0 0 9px #666;
}
.white{
  color: white;
}
</style>



