<template>
<div id="GrammarVue">
  <div class="moble" @click='methodName'>
    <h2 class='color_red'>生命周期:</h2>
    <p>constructor(): {super(props)}
      <span class="color_red">构造函数在挂载之前被调用</span><br/>
      <span>this.state</span>
    </p>
    <p>Create：
      <span class="color_red">完成了 data 数据的初始化，el没有</span>
    </p>
    <p>beforeMount：
      <span class="color_red">完成了 el 和 data 初始化  模版数据未编译</span>
    </p>
    <p>Mount：
      <span class="color_red">完成挂载编译</span>
    </p>
    <p>beforeUpdate：
      <span class="color_red">组件更新之前</span>
    </p>
    <p>Update：
      <span class="color_red">组件更新之后</span>
    </p>
    <p>activated：
      <span class="color_red">keep-alive组件激活时调用。</span>
    </p>
    <p>deactivated:
      <span class="color_red">keep-alive组件停用时调用。</span>
    </p>
    <p>beforeDestroy:
      <span class="color_red">实例销毁之间调用。在这一步，实例仍然完全可用。</span>
    </p>
    <p>destroyed:
      <span class="color_red">Vue实例销毁后调用。调用后，Vue实例指示的所有东西都会解绑定，所有的事件监听器会被移除，所有的子实例也会被销毁。</span>
    </p>
  </div>
</div>
</template>
<script>
export default {
  data: () => ({

  }),
  methods: {
    methodName() {
      console.log('222');
    }
  }
}
</script>
<style lang="scss" scoped>
.highlight {
  height: 320px;
  overflow-x: auto;
  background-color: #f8f8f8;
  border-radius: 2px;
  position: relative;
}

.code {
  width: 100%;
  border-collapse: collapse;
  padding: 0;
  margin: 0;
}
</style>



