<template>
  <div id="header">
    <Row>
      <Col span="2" offset='2'>
        <router-link to='/'>
          <img class="logo" src="@/assets/logo.png" alt="logo">
        </router-link>
      </Col>
        <Col span="18" class="right">
          <Menu mode="horizontal" :theme="theme1" active-name="0">
            <MenuItem name="1">
              <router-link class="router_link" to='/html'>
                HTML
              </router-link>
            </MenuItem>
            <MenuItem name="2">
              <router-link class="router_link" to='/Css'>
                CSS
              </router-link>
            </MenuItem>
              <MenuItem name="3">
                <router-link class="router_link" to="/JavaScript">
                  JavaScript
                </router-link>
              </MenuItem>
              <MenuItem name="4">
                <router-link class="router_link" to='/Git'>
                  Git
                </router-link>
              </MenuItem>
              <MenuItem name="5">
                <router-link class="router_link" to='/vue'>
                  Vue
                </router-link>
              </MenuItem>
             </Menu>
        </Col>
    </Row>
  </div>
</template>
<script>
import { Row, Col, Menu, MenuItem } from 'iview';
export default {
  components:{
    Row, Col, Menu, MenuItem
  },
  name: "",
  data: () => ({
    theme1: 'light'
  })
}
</script>
<style scoped>
#header{
  width: 100%;
}
.router_link{
  width: 100%;
  height: 100%;
  display: block;
}
.logo{
  position: relative;
  top: 15px;
  width: 64px;
  height: 64px;
  margin: auto;
  line-height: 64px;
  animation: App-logo-spin infinite 3s linear;
}
.right{
  margin-top: 20px;
}
@keyframes App-logo-spin {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
      -webkit-transform: rotate(1turn);
      transform: rotate(1turn);
  }
}
</style>



